/* min-width 1025px, large screens */
/* min-width 1024px and max-width 1440px, use when QAing large screen-only issues */
$break-1440: 1440px;
$break-1200: 1200px;
$break-1024: 64.063em;
$break-810: 810px;
$break-800: 800px;
$break-769: 769px;
$break-732:732px;
$break-568: 35.5em;
$break-480: 480px;
$break-414: 414px;
$break-320: 320px;
$break-375: 375px;
$break-309: 309px;


$break-1085: 1085px;
$break-657: 657px;

@mixin respond-to($media){
    @if $media == lgr-screen{
        @media only screen 
            and (max-width: $break-1024 + 1) 
            { @content;} 
    }
    @else if $media == lg-screen{
        @media only screen 
            and (max-width: $break-1085 - 1) 
            { @content; } 
    }
    @else if $media == md-screen{
      @media only screen 
          and (max-width: $break-732) 
          { @content; } 
    }
    @else if $media == sm-screen{
      @media only screen 
          and (max-width: $break-414) 
          { @content; } 
    }
    @else if $media == xs-screen{
      @media only screen 
          and (max-width: $break-309) 
          { @content; } 
    }
    
    @else if $media == svg-max{
      @media only screen 
          and (max-width: $break-1440) 
          and (min-width: $break-1200) 
          { @content; } 
    }
// PORTRAIT SPECIFIC
    @else if $media == tablet-portrait{
        @media screen 
            and (min-width: $break-480) 
            and (orientation:portrait) 
            { @content; }
    }
    @else if $media == tablet-portrait-wide{
      @media screen 
          and (min-width: $break-732) 
          and (orientation:portrait) 
          { @content; }
  }
  @else if $media == tablet-portrait-wide-xl{
    @media screen 
        and (min-width: $break-800) 
        and (orientation:portrait) 
        { @content; }
}
    @else if $media == tablet-landscape{
        @media screen 
            and (min-width: $break-568 + 1) 
            and (orientation:landscape) 
            { @content; }
    }
    
    @else if $media == tablet-landscape-wide{
        @media screen 
            and (min-width: $break-769) 
            and (orientation:landscape) 
            { @content; }
    }
    @else if $media == mobile-portrait-xl{
      @media screen 
          and (max-width: $break-480 - 1) 
          and (orientation:portrait) 
          { @content; }
    }
    @else if $media == mobile-portrait{
        @media screen 
            and (max-width: $break-375) 
            and (orientation:portrait) 
            { @content; }
    }
    @else if $media == mobile-portrait-xs{
      @media screen 
          and (max-width: $break-320 + 1) 
          and (orientation:portrait) 
          { @content; }
    }
    @else if $media == mobile-landscape{
        @media screen 
            and (max-width: $break-568) 
            and (orientation:landscape) 
            { @content; }
    }
}
