@import '../variables/colors';
@import '../variables/font';
@import '../abstracts/invertColor';
@import '../elements/input';
@import '../elements/button';
.CitySearch {
  padding:2em 2em 0 2em;
  color: $color-lighter;
  &__err{
    color:green;
    font-size: 3rem;
    z-index: -1;
    position: absolute;
    margin:1em 0;
    min-height: 100%;
    top:7em;
    max-width:100%;
  }
  &__label{
    font-size: $font-4;
    display:block;
    color:$color-light;
    margin-bottom:.4em;
    @include respond-to(lg-screen){
      font-size: $font-3;
    }
  }
  
  &__btn {
    position: fixed;
    z-index: 200;
    top:3.8em;
    right:1.7em;
    margin: 0 1em 3em 0;
    @extend button-element;
    @include respond-to(mobile-portrait){
      margin: 0 0em 2em 0;
    }
  }
  
  input{
    @extend input-element;
    @include respond-to(lg-screen){
      font-size: $font-3;
    }
  }
}

