@import '../abstracts/mediaQueries';
@import '../variables/colors';
@import '../variables/font';
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  position: relative;
  display:grid;
  place-content: center;
  overflow-x: hidden;
  @include respond-to(lg-screen){
    display:block;
    height:auto;
  }
  // navbar & footer
  .navbar {
    p {
      font-size: $font-3;
    }
  }
  .navbar{
    color: $color-lighter;
  }
  &__background{
    bottom:0;
    left:-20em;
    opacity: .75;
    z-index: -1;
    position: absolute;
    transform:scale(1) translate(-19em, 0em);
    -webkit-transform:scale(1) translate(-19em, 0em);
    -moz-transform:scale(1) translate(-19em, 0em);
    -ms-transform:scale(1) translate(-19em, 0em);
    -o-transform:scale(1) translate(-19em, 0em);
    
    @include respond-to(tablet-landscape-wide){
      bottom:unset;
      transform:translateY(-12em)    ;
      -webkit-transform:translateY(-12em)    ;
      -moz-transform:translateY(-12em)    ;
      -ms-transform:translateY(-12em)    ;
      -o-transform:translateY(-12em)    ;
    }
    @include respond-to(tablet-landscape){
      transform:scale(.7) translate(-20em, -43em);
      -webkit-transform:scale(.7) translate(-20em, -43em);
      -moz-transform:scale(.7) translate(-20em, -43em);
      -ms-transform:scale(.7) translate(-20em, -43em);
      -o-transform:scale(.7) translate(-20em, -43em);
    }
    @include respond-to(mobile-landscape){
      transform:scale(.4) translate(-85em, -77em);
      -webkit-transform:scale(.4) translate(-85em, -77em);
      -moz-transform:scale(.4) translate(-85em, -77em);
      -ms-transform:scale(.4) translate(-85em, -77em);
      -o-transform:scale(.4) translate(-85em, -77em);
    }
    @include respond-to(tablet-portrait-wide){
      transform:scale(.8) translate(-20em, -10em);
      -webkit-transform:scale(.8) translate(-20em, -10em);
      -moz-transform:scale(.8) translate(-20em, -10em);
      -ms-transform:scale(.8) translate(-20em, -10em);
      -o-transform:scale(.8) translate(-20em, -10em);
    }
    @include respond-to(tablet-portrait){
      transform:scale(.8) translate(-20em, -10em);
      -webkit-transform:scale(.8) translate(-20em, -10em);
      -moz-transform:scale(.8) translate(-20em, -10em);
      -ms-transform:scale(.8) translate(-20em, -10em);
      -o-transform:scale(.8) translate(-20em, -10em);
}
    @include respond-to(mobile-portrait-xl){
      transform:scale(.8) translate(-25em, -25em);
      -webkit-transform:scale(.8) translate(-25em, -25em);
      -moz-transform:scale(.8) translate(-25em, -25em);
      -ms-transform:scale(.8) translate(-25em, -25em);
      -o-transform:scale(.8) translate(-25em, -25em);
    }
    @include respond-to(mobile-portrait){
      transform:scale(.8) translate(-25em, -25em);
      -webkit-transform:scale(.8) translate(-25em, -25em);
      -moz-transform:scale(.8) translate(-25em, -25em);
      -ms-transform:scale(.8) translate(-25em, -25em);
      -o-transform:scale(.8) translate(-25em, -25em);
    }
    @include respond-to(mobile-portrait-xs){
      transform:scale(.8) translate(-30em, -35em);
      -webkit-transform:scale(.8) translate(-30em, -35em);
      -moz-transform:scale(.8) translate(-30em, -35em);
      -ms-transform:scale(.8) translate(-30em, -35em);
      -o-transform:scale(.8) translate(-30em, -35em);
}
}
  
  @include respond-to(tablet-landscape){
  }
  @include respond-to(mobile-landscape){
  }
  
  // main
  .main__container{
    font-size: $font-2;
    display:flex;
    // height:100vh;
    justify-content: center;
    align-items:flex-start;
    margin: 0;
    width: 100vw;
    @include respond-to(lg-screen){
      align-items: flex-start;
      flex-direction: column;
    }
    @include respond-to(tablet-landscape){
      // min-height: 100vh;
    }
    @include respond-to(mobile-portrait){
      min-height: 100vh;
    }
  }
  .input__container{
    display: block;
    text-align: left;
    width: 53.94vw;
    
    @include respond-to(lg-screen){
      // display: grid;
      grid-template-columns: 1.5fr 0.9fr;
      grid-template-rows: 0.5fr;
      gap: 2em 2em;
      grid-auto-flow: row;
      // align-content: space-evenly;
      justify-items: stretch;
      align-items: stretch;
      grid-template-areas:
        ". .";
      
      
      &-inner{
        margin: 0 auto;
        @include respond-to(lg-screen){
          width: 90vw;
        }
        @include respond-to(sm-screen){
          flex-direction: column;
        }
      }
    }
    @include respond-to(tablet-landscape){
      // margin-top:-2em;
    }
    @include respond-to(mobile-landscape){
      margin-top:-2em;
    }
    @include respond-to(mobile-portrait){
      margin-top:-2em;
    }
  }
}


