@import '../abstracts/mediaQueries';
@import '../variables/colors';
@import '../variables/font';
@import '../elements/input';
@import '../elements/button';
.navbar {
  display:flex;
  align-items: center;
  justify-content: space-between;
  padding:2em 2.5em;
  a{
    text-decoration: none;
    font-size:1rem;
    @include respond-to(tablet-portrait){
      font-size:.8rem;
    }
    @include respond-to(mobile-portrait-xl){
      font-size: .5rem;
    }
    @include respond-to(mobile-portrait-xs){
      font-size: .5rem;
    }
    
  }
  p{
    color:$color-lighter;
  }
  &__logo-highlight{
    color: $color-light;
  }
}